* {
  margin: 0;
  padding: 0;
}
body {
  background-color: #05386b05;
  background-image: url("https://www.transparenttextures.com/patterns/dark-dotted-2.png");

}
/* ::-webkit-scrollbar-track {
  box-shadow: outset 0 0 4px #05386bde; 
} */
.tracker {
  overflow-x: hidden !important;
}
::-webkit-scrollbar {
  width: 15px;
}

::-webkit-scrollbar-thumb {
  background: #05386bd7;
}

::-webkit-scrollbar-thumb:hover {
  background: #05386b42;
}
h1 {
  font-size: 3rem;
}
h1:hover {
  cursor: pointer;
  color: #05386b;
  transform: scale(1.1);
  transition: 0.2s ease-in;
}
h10 {
  display: block;
  color: #000000d2;
  font-size: 1.1em;
}
h6 {
  display: block;
  font-size: 1.17em;
  margin-left: 10px;
  font-weight: bold;
}
.img {
  font-size: 3.3rem;
  text-align: center;
}
.image {
  position: relative;
  top: 13px;
  left: -4px;
  margin-right: -8px;
  width: 50px;
  height: 50px;

  /* padding-top: 10px; */
  /* margin:-60px 0px 0px -10px; */
  -webkit-animation: spin 4s linear infinite;
  -moz-animation: spin 4s linear infinite;
  animation: spin 4s linear infinite;
}
@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
body::-webkit-scrollbar {
  display: none;
}
.select {
  background-color: #ffffff;
  min-width: 150px;
}
.select:hover {
  transform: scale(1.03);
  transition: 0.1s ease-in;
}
.menu:hover {
  background-color: #05386b !important;
  opacity: 0.9;
  color: #edf5e1;
}
.app {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}
.app__header {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 30px;
  align-items: center;
}
.app__stats {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 30px;
}
.graph {
  padding-left: 9rem;
  padding-right: 9rem;
  justify-content: center;
}
.app__left {
  padding-top: 2rem;
  flex: 0.85;
}
.app__right {
  padding-left: 20px;
  flex: 0.4;
}

@media (max-width: 1200px) {
  .graph {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}
@media (max-width: 1000px) {
  .app {
    flex-direction: column;
  }
  .app__left {
    padding: 0;
    margin-bottom: 2rem;
  }
  .app__right {
    padding-left: 60px;
    padding-right: 60px;
  }
}
@media (max-width: 576px) {
  .app__stats {
    padding-left: 2rem;
    padding-right: 2rem;
    flex-direction: column;
  }
  .app__header {
    flex-direction: column;
  }
  .app__header h1 {
    margin-top: 10px;
    margin-bottom: 20px;
  }
  .app__right {
    padding: 0;
  }
  .graph {
    padding: 0;
  }
}
