@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
* {
  margin: 0;
  padding: 0;
}
body {
  background-color: #05386b05;
  background-image: url("https://www.transparenttextures.com/patterns/dark-dotted-2.png");

}
/* ::-webkit-scrollbar-track {
  box-shadow: outset 0 0 4px #05386bde; 
} */
.tracker {
  overflow-x: hidden !important;
}
::-webkit-scrollbar {
  width: 15px;
}

::-webkit-scrollbar-thumb {
  background: #05386bd7;
}

::-webkit-scrollbar-thumb:hover {
  background: #05386b42;
}
h1 {
  font-size: 3rem;
}
h1:hover {
  cursor: pointer;
  color: #05386b;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: 0.2s ease-in;
}
h10 {
  display: block;
  color: #000000d2;
  font-size: 1.1em;
}
h6 {
  display: block;
  font-size: 1.17em;
  margin-left: 10px;
  font-weight: bold;
}
.img {
  font-size: 3.3rem;
  text-align: center;
}
.image {
  position: relative;
  top: 13px;
  left: -4px;
  margin-right: -8px;
  width: 50px;
  height: 50px;

  /* padding-top: 10px; */
  /* margin:-60px 0px 0px -10px; */
  -webkit-animation: spin 4s linear infinite;
  animation: spin 4s linear infinite;
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
body::-webkit-scrollbar {
  display: none;
}
.select {
  background-color: #ffffff;
  min-width: 150px;
}
.select:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  transition: 0.1s ease-in;
}
.menu:hover {
  background-color: #05386b !important;
  opacity: 0.9;
  color: #edf5e1;
}
.app {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  padding: 20px;
}
.app__header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-bottom: 30px;
  -webkit-align-items: center;
          align-items: center;
}
.app__stats {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-bottom: 30px;
}
.graph {
  padding-left: 9rem;
  padding-right: 9rem;
  -webkit-justify-content: center;
          justify-content: center;
}
.app__left {
  padding-top: 2rem;
  -webkit-flex: 0.85 1;
          flex: 0.85 1;
}
.app__right {
  padding-left: 20px;
  -webkit-flex: 0.4 1;
          flex: 0.4 1;
}

@media (max-width: 1200px) {
  .graph {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}
@media (max-width: 1000px) {
  .app {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .app__left {
    padding: 0;
    margin-bottom: 2rem;
  }
  .app__right {
    padding-left: 60px;
    padding-right: 60px;
  }
}
@media (max-width: 576px) {
  .app__stats {
    padding-left: 2rem;
    padding-right: 2rem;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .app__header {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .app__header h1 {
    margin-top: 10px;
    margin-bottom: 20px;
  }
  .app__right {
    padding: 0;
  }
  .graph {
    padding: 0;
  }
}

.infoBox {
  cursor: pointer;
}
.infoBox:hover{
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  transition: 0.2s ease-in;
}
.infoBox__1{
  border-top: 5px solid #f5a962;
  border-bottom: 5px solid #f5a962;
}
.infoBox__2{
  border-top: 5px solid #66de93;
  border-bottom: 5px solid #66de93;
}
.infoBox__3{
  border-top: 5px solid #ff616d;
  border-bottom: 5px solid #ff616d;
}
.info__1:hover{
  box-shadow: 0 0 5px 2px #f5a9627e;
}
.info__2:hover{
  box-shadow: 0 0 5px 2px #66de9480;
}
.info__3:hover{
  box-shadow: 0 0 5px 2px #ff616e80;
}
.infoBox__cases {
  font-weight: 550;
  font-size: 1.5rem;
  margin-bottom: 0.9rem;
}
.cases1{
  color: #f5a962;
}
.cases2{
  color: #66de93;
}
.cases3{
  color: #ff616d;
}
.infoBox__total {
  opacity: 0.7;
}
h5{
  padding-top: 0.5rem;
  color: #052d55;
}
.infoBox__title {
  font-weight: 1000;
}
@media (max-width: 576px)
{
  .infoBox {
  padding-bottom: 1rem;
}
}

.table {
  margin-top: 10px;
  overflow-y: scroll;
  height: 350px;
}
.table tr {
  display: -webkit-flex;
  display: flex;
  margin-left: 10px;
  margin-right: 10px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.table td {
  padding: 0.6rem;
}
.table tr:nth-of-type(odd) {
  background-color: #05386B;
  opacity: 0.9;
  color: #EDF5E1;
}

.news {
  margin-top: 0px;
  overflow-y: scroll;
  height: 180px;
}
.tr {
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0.2rem;
  margin: 10px;
}
.tr:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  transition: 0.2s ease-in;
  box-shadow: 0 0 8px 4px #05386b46;
}
.tr:nth-of-type(odd) {
  background-color: #05386B;
  opacity: 0.9;
  color: #EDF5E1;
}

.line__wrapper {
  border-radius: 5px;
  background-color: rgb(255, 255, 255);
}
.line__wrapper:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  transition: 0.4s ease-in;
}
.gr {
  box-shadow: 0 0 10px 5px #66de9480;
}
.gr:hover {
  box-shadow: 0 0 10px 5px #66de93;
}
.rd {
  box-shadow: 0 0 10px 5px #ff616e80;
}
.rd:hover {
  box-shadow: 0 0 10px 5px #ff616d;
}
.yl {
  box-shadow: 0 0 10px 5px #f5a9627e;
}
.yl:hover {
  box-shadow: 0 0 10px 5px #f5a962;
}

.footer {
  height: 25vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-top: 30px;
  padding-left: 10px;
  padding-right: 10px;
}
p {
  font-size: 0.9rem;
}
.cp{
  color:#EDF5E1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  padding: 0.4rem;
  background: #05386B;
}
.social_links {
  padding: 0.2rem;
  background: #05386B;
  border-radius: 1rem;
  margin-top: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}
.social_links .combo {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: 0.4s ease-in;
  margin: 0 0.5rem;
  height: 2em;
  width: 2em;
  line-height: 2em;
}
.combo .circle {
  color: #EDF5E1;
  transition: 0.4s ease-in;
  font-size: 2em;
}
.combo .icon {
  color: #05386B;
  font-size: 1.5rem;
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  transition: 0.2s ease-in;
}
.combo:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.combo:hover,
.combo:hover .icon {
  color: white;
}
.ig:hover,
.ig:hover .circle {
  color: #e91e63;
}
.fb:hover,
.fb:hover .circle {
  color: #2196f3;
}
.yt:hover,
.yt:hover .circle {
  color: #f44336;
}
.tw:hover,
.tw:hover .circle {
  color: #2196f3;
}
.db:hover,
.db:hover .circle {
  color: #ffc107;
}
.ml:hover,
.ml:hover .circle {
  color: #b6854d;
}
.gt:hover,
.gt:hover .circle {
  color: #505050;
}

